<script>
import { getPostBoxcoinRecords, recoveryBoxcoin } from "../../service/thx";
import User from "@jx3box/jx3box-common/js/user";
import { showAvatar, authorLink } from "@jx3box/jx3box-common/js/utils";
import { showTime } from "@jx3box/jx3box-common/js/moment";
export default {
    name: "BoxcoinRecords",
    props: ["postType", "postId", "cacheRecord", "postClient", "mode"],
    components: {},
    data: function () {
        return {
            list: [],
            per: 12,
            page: 1,
            total: 1,
            loading: false,
            boxcoin: 0,
            isSuperAdmin: User.isSuperAdmin(),
        };
    },
    computed: {
        post_keys: function () {
            return [this.postType, this.postId];
        },
        params: function () {
            return {
                pageSize: this.per,
                pageIndex: this.page,
                //client : this.postClient,
            };
        },
    },
    watch: {
        post_keys: {
            immediate: true,
            deep: true,
            handler: function () {
                this.postType && this.postId && this.loadData();
            },
        },
        params: {
            deep: true,
            handler: function () {
                this.loadData();
            },
        },
        cacheRecord: {
            deep: true,
            handler: function (data) {
                if (data) {
                    this.list.push(data);
                    // 清空父组件的cache
                    this.$parent.cacheRecord = null;
                }
            },
        },
    },
    methods: {
        loadData: function () {
            getPostBoxcoinRecords(this.postType, this.postId, this.params).then((res) => {
                this.list = res.data.data?.list || [];
                this.total = res.data.data.page.total;
                this.boxcoin = res.data.data.fromManager + res.data.data.fromUser;
                this.$parent.boxcoin = this.boxcoin;
            });
        },
        recovery: function (item, i) {
            this.$alert("是否确定撤销该评分？", "确认", {
                confirmButtonText: "确定",
                callback: (action) => {
                    if (action == "confirm") {
                        recoveryBoxcoin(item.id).then((res) => {
                            this.$message({
                                message: "撤销成功",
                                type: "success",
                            });
                            this.list.splice(i, 1);
                        });
                    }
                },
            });
        },
        authorLink,
        showTime,
        showAvatar: function (val) {
            return showAvatar(val, 72);
        },
    },
};
</script>
